<template>
  <div>
    <el-page-header
      style="margin-bottom: 5px; margin-left: 5px; margin-top: 5px; float: left"
      @back="back"
      content=""
    >
    </el-page-header>
    <!-- <el-button
      style="margin-bottom: 5px; margin-left: 5px; margin-top: 5px; float: left"
      @click="back"
      size="mini"
      ></el-button
    > -->
    <el-descriptions
      contentStyle="width:500px"
      style="margin-left: 5px; margin-top: 5px"
      :column="2"
      border
      height="400"
    >
      <el-descriptions-item label=""
        ><el-col :span="8"><el-input v-model="course.name"></el-input></el-col>
      </el-descriptions-item>
      <el-descriptions-item label="id">{{
        course.id
      }}</el-descriptions-item>
      <el-descriptions-item label="">
        <div
          :key="index"
          v-for="(day, index) in course.week"
          style="float: left; margin-left: 10px"
        >
          <el-checkbox size="mini" :label="day" v-model="course.week[index]">{{
            "" + week[index]
          }}</el-checkbox>
        </div>
        <br />
        <div :key="k" v-for="(day, k) in week">
          <div v-show="course.week[k]">
            {{ day }}
            <!-- <el-time-picker
              is-range
              size="small"
              v-model="course.time[k]"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              placeholder=""
              format="HH:mm"
              value-format="HH:mm"
            ></el-time-picker> -->
            <el-select v-model="course.time[k][0]" placeholder="">
              <el-option
                v-for="item in options1"
                :key="item"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
            <el-select v-model="course.time[k][1]" placeholder="">
              <el-option
                v-for="item in options2"
                :key="item"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </div>
        </div>
      </el-descriptions-item>
      <el-descriptions-item :span="2" label="">
        <el-col :span="20">
          <el-input
            size="small"
            type="number"
            v-model="course.classes"
            readonly
          ></el-input>
        </el-col>
      </el-descriptions-item>
      <el-descriptions-item label="">
        <el-dropdown @command="chooseplace">
          <span class="el-dropdown-link">
            {{ showplace }}<i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="19"></el-dropdown-item>
            <el-dropdown-item command="20">1</el-dropdown-item>
            <el-dropdown-item command="21">2xx</el-dropdown-item>
            <el-dropdown-item command="22">3</el-dropdown-item>
            <el-dropdown-item command="23">4</el-dropdown-item>
            <el-dropdown-item command="128">S1</el-dropdown-item>
            <el-dropdown-item command="129">S2,S3</el-dropdown-item>
            <el-dropdown-item command="131"
              ></el-dropdown-item
            >
          </el-dropdown-menu>
        </el-dropdown>
      </el-descriptions-item>
      <el-descriptions-item label=""
        ><el-col :span="8"
          ><el-input v-model="course.place[1]"></el-input></el-col
      ></el-descriptions-item>
      <el-descriptions-item label=""
        ><el-col :span="8"
          ><el-input v-model="course.teacher"></el-input></el-col
      ></el-descriptions-item>
      <el-descriptions-item label=""
        ><el-input type="textarea" autosize v-model="course.info[0]"></el-input
      ></el-descriptions-item>
      <el-descriptions-item label="">
        <el-input type="textarea" autosize v-model="course.info[2]"></el-input
      ></el-descriptions-item>
      <el-descriptions-item label=""
        ><el-input type="textarea" autosize v-model="course.info[1]"></el-input
      ></el-descriptions-item>
      <!-- <el-descriptions-item label="">
        <div></div>
        <el-divider></el-divider>
        <div></div></el-descriptions-item
      > -->
      <el-descriptions-item label=""
        ><el-upload
          class="upload-demo"
          action="http://localhost:3000/workfile"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :before-remove="beforeRemove"
          multiple
          :limit="1"
          :on-exceed="handleExceed"
          :file-list="fileList"
          :http-request="upload"
        >
          <el-button size="small" type="primary"></el-button>
          <div slot="tip" class="el-upload__tip">
            doc/txt/png,500kb
          </div> </el-upload
        ><el-button size="small" type="primary" @click="newwork"
          ></el-button
        ></el-descriptions-item
      >
      <el-descriptions-item label=""
        ><el-button size="small" @click="check"></el-button
        ><br /><br />
        <el-col :span="20"
          ><el-input
            size="small"
            type="number"
            v-model="studentid"
            placeholder=""
          ></el-input></el-col
        ><el-button size="small" @click="download"
          ></el-button
        ></el-descriptions-item
      >
    </el-descriptions>
    <el-button
      type="success"
      style="margin-bottom: 5px; margin-left: 5px; margin-top: 5px; float: left"
      @click="confirm"
      size="mini"
      ></el-button
    >
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      course: {
        name: "",
        id: 0,
        classes: "",
        // week: [true,false,false,true,false,false,false],
        week: [false, false, false, false, false, false, false],
        time: [
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
        ],
        place: [0, ""],
        teacher: "",
        info: ["", "", ""],
        url: "/course/0",
      },
      sid: "",
      week: ["", "", "", "", "", "", ""],
      work: false,
      studentid: "",
      placename: new Array(200),
      void: "",
      options1: [
        "08:00",
        "08:50",
        "09:50",
        "10:40",
        "11:30",
        "13:00",
        "13:50",
        "14:45",
        "15:40",
        "16:35",
        "17:25",
        "18:30",
        "19:20",
        "20:10",
      ],
      options2: [
        "08:45",
        "09:35",
        "10:35",
        "11:25",
        "12:15",
        "13:45",
        "14:35",
        "15:30",
        "16:25",
        "17:20",
        "18:10",
        "19:15",
        "20:05",
        "20:55",
      ],
    };
  },
  computed: {
    showplace: {
      get() {
        this.course.place[0];
        return this.placename[this.course.place[0]] + this.void;
      },
    },
  },
  methods: {
    chooseplace(command) {
      this.course.place[0] = command;
      this.void = " ";
      this.void = "";
      // this.showplace();
    },
    newwork() {
      this.work = true;
    },
    check() {
      let that = this;
      axios
        .post("http://localhost:3000/course/check", {
          sid: that.sid,
          course: that.course,
        })
        .then((res) => {
          // console.log(res.data);
          alert(res.data);
        })
        .catch((err) => {
          console.log(err);
          this.$message({
            type: "error",
            message: "",
          });
        });
    },
    download(param) {
      let that = this;
      let fileobj = param.file;
      let form = new FormData();
      form.append("file", fileobj);
      // form.append("params", that.course.id);
      console.log(form);
      axios({
        method: "post",
        url:
          "http://localhost:3000/course/download/" +
          that.course.id +
          "/" +
          that.studentid,
        headers: { "content-type": "multipart/form-data" },
      })
        .then((res) => {
          console.log(res.data);

          window.open("http://localhost:3000" + res.data);
        })
        .catch((err) => {
          console.log(err);
          this.$message({ type: "error", message: "" });
        });
    },
    back() {
      this.$destroy();
      this.$router.replace({ name: "DisplayCourses" });
    },
    confirm() {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          let that = this;
          that.course.place[0] = parseInt(that.course.place[0]);
          axios
            .post("http://localhost:3000/course/edit", {
              sid: that.sid,
              course: that.course,
              work: that.work,
            })
            .then((res) => {
              console.log(res.data);
              if (res.data == "0")
                this.$message({
                  type: "error",
                  message: "!",
                });
              else if (res.data == "1")
                this.$message({
                  type: "success",
                  message: "!",
                });
            })
            .catch((err) => {
              console.log(err);
              this.$message({
                type: "error",
                message: "",
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });

      this.$router.replace({ name: "DisplayCourses" });
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        ` 1  ${files.length}  ${
          files.length + fileList.length
        } `
      );
    },
    beforeRemove(file, fileList) {
      console.log(fileList);
      return this.$confirm(" ${file.name}?");
    },
    upload(param) {
      let that = this;
      let fileobj = param.file;
      let form = new FormData();
      form.append("file", fileobj);
      // form.append("params", that.course.id);
      console.log(form);
      axios({
        method: "post",
        url: "http://localhost:3000/course/workfile/" + that.course.id,
        headers: {
          "Content-Type": "multipart/form-data",
        },
        data: form /* { file: form, id: that.course.id } */,
        params: that.course.id,
      })
        .then((res) => {
          alert(res.data);
        })
        .catch((err) => {
          console.log(err);
          this.$message({ type: "error", message: "" });
        });
    },
  },
  mounted() {
    console.log(this.$route.params);
    this.placename[19] = "";
    this.placename[20] = "1";
    this.placename[21] = "2";
    this.placename[22] = "3";
    this.placename[23] = "4";
    this.placename[128] = "";
    this.placename[129] = "";
    this.placename[131] = "";

    // this.sid = JSON.parse(sessionStorage.getItem("user"));
    let that = this;
    this.sid = JSON.parse(sessionStorage.getItem("user"));
    console.log(this.$route.params);
    axios
      .post("http://localhost:3000/course/view", {
        cid: that.$route.params.id,
        sid: sessionStorage.getItem("user"),
      })
      .then((res) => {
        that.course = res.data;
        that.course.place[0] = "" + that.course.place[0];
      })
      .catch((err) => {
        console.log(err);
        that.$message({
          type: "error",
          message: "",
        });
      });
  },
  beforeCreate() {},
};
</script>



<style scoped>
</style>