<template>
  <div>
    <!-- <el-button
      style="margin-bottom: 5px; margin-left: 5px; margin-top: 5px; float: left"
      @click="back"
      size="mini"
      ></el-button
    > -->
    <el-page-header
      style="margin-bottom: 5px; margin-left: 5px; margin-top: 5px; float: left"
      @back="back"
      content=""
    >
    </el-page-header>
    <el-descriptions
      contentStyle="width:500px"
      style="margin-left: 5px; margin-top: 5px"
      :column="2"
      border
      height="400"
    >
      <el-descriptions-item label=""
        >{{ course.name }}
      </el-descriptions-item>
      <el-descriptions-item label="id">{{
        course.id
      }}</el-descriptions-item>
      <el-descriptions-item label="">
        <div v-for="(day, index) in week" :key="index">
          <div v-show="course.week[index]">
            {{ day }}
            <el-time-picker
              readonly
              is-range
              size="small"
              v-model="course.time[index]"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              placeholder=""
              format="HH:mm"
              value-format="HH:mm"
            ></el-time-picker>
          </div>
        </div>
      </el-descriptions-item>
      <el-descriptions-item :span="2" label="">
        <el-col :span="8">
          <el-input
            size="small"
            type="number"
            v-model="course.classes"
            readonly
          ></el-input>
        </el-col>
      </el-descriptions-item>
      <el-descriptions-item label="">{{
        course.place[1]
      }}</el-descriptions-item>
      <el-descriptions-item label="">{{
        course.teacher
      }}</el-descriptions-item>
      <el-descriptions-item label=""
        ><el-input
          readonly
          type="textarea"
          autosize
          v-model="course.info[0]"
        ></el-input
      ></el-descriptions-item>
      <el-descriptions-item label=""
        ><el-input
          readonly
          type="textarea"
          autosize
          v-model="course.info[2]"
        ></el-input>
      </el-descriptions-item>
      <el-descriptions-item label=""
        ><el-input
          readonly
          type="textarea"
          autosize
          v-model="course.info[1]"
        ></el-input>
        <el-button size="small" @click="download"></el-button>
      </el-descriptions-item>
      <el-descriptions-item label=""
        ><el-upload
          class="upload-demo"
          action="http://localhost:3000/file"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :before-remove="beforeRemove"
          multiple
          :limit="1"
          :on-exceed="handleExceed"
          :file-list="fileList"
          :http-request="upload"
        >
          <el-button size="small" type="primary"></el-button>
          <div slot="tip" class="el-upload__tip">
            doc/txt/png,500kb
          </div>
        </el-upload></el-descriptions-item
      >
      <!-- <el-descriptions-item label="">
        <div></div>
        <el-divider></el-divider>
        <div></div></el-descriptions-item
      > -->
    </el-descriptions>
    <el-button
      type="success"
      style="margin-bottom: 5px; margin-left: 5px; margin-top: 5px; float: left"
      @click="confirm"
      size="mini"
      ></el-button
    >
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      course: {
        name: "",
        id: 0,
        classes: "",
        // week: [true,false,false,true,false,false,false],
        week: [false, false, false, false, false, false, false],
        time: [
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
        ],
        place: [-1, ""],
        teacher: "",
        info: ["", "", ""],
        url: "/course/0",
      },
      week: ["", "", "", "", "", "", ""],
      fileList: [],
      //   fileList: [{name:"food.jpg",url:""}]
    };
  },
  methods: {
    back() {
      // this.$destroy();
      this.$router.replace({ name: "DisplayCourses" });
    },
    confirm() {
      // this.$destroy();
      this.$router.replace({ name: "DisplayCourses" });
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        ` 3  ${files.length}  ${
          files.length + fileList.length
        } `
      );
    },
    beforeRemove(file, fileList) {
      console.log(fileList);
      return this.$confirm(" ${file.name}?");
    },
    upload(param) {
      let that = this;
      let fileobj = param.file;
      let form = new FormData();
      form.append("file", fileobj);
      // form.append("params", that.course.id);
      console.log(form);
      axios({
        method: "post",
        url:
          "http://localhost:3000/course/file/" +
          that.course.id +
          "/" +
          sessionStorage.getItem("user"),
        headers: {
          "content-type": "multipart/form-data",
        },
        // params: that.course.id,
        data:form,
      })
        .then((res) => {
          alert(res.data);
        })
        .catch((err) => {
          console.log(err);
          this.$message({ type: "error", message: "" });
        });
    },
    download(param) {
      let that = this;
      let fileobj = param.file;
      let form = new FormData();
      form.append("file", fileobj);
      // form.append("params", that.course.id);
      console.log(form);
      axios({
        method: "get",
        url: "http://localhost:3000/course/workfile/" + that.course.id + '/'+ that.$route.params.sid,
        headers: { "content-type": "multipart/form-data" },
      })
        .then((res) => {
          console.log(res.data);
          if (res.data == 0) {
            this.$message({ type: "error", message: "" });
            return;
          }
          console.log(res.data)
          window.open(("http://localhost:3000" + res.data));
        })
        .catch((err) => {
          console.log(err);
          this.$message({ type: "error", message: "" });
        });
    },
  },
  mounted() {
    console.log(this.$route.params);

    let that = this;
    console.log(this.$route.params);
    axios
      .post("http://localhost:3000/course/view", {
        cid: that.$route.params.id,
        sid: sessionStorage.getItem("user"),
      })
      .then((res) => {
        that.course = res.data;
      })
      .catch((err) => {
        console.log(err);
        that.$message({
            type: "error",
            message: "",
          });
      });
  },
  beforeCreate() {
    
  },
};
</script>








