<template >
  <div>
    <!--  -->
    <div>
      <el-menu class="el-menu-demo" mode="horizontal"></el-menu>
      <div class="line"></div>
      <el-menu
        class="el-menu-demo"
        mode="horizontal"
        background-color="#545c64"
        text-color="#fff"
      >
        <el-menu-item></el-menu-item>
      </el-menu>
    </div>
    <!--  -->
    <el-container style="height: 660px; border: 1px solid #eee"
      ><!-- css -->
      <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
        <el-menu :default-openeds="['1', '2', '3']" router>
          <el-submenu index="1">
            <template slot="title"
              ><i class="el-icon-realdate"></i></template
            >
            <el-menu-item-group>
              <!-- indexrouterindex.jsindex -->
              <el-menu-item index="/Courses"></el-menu-item>
              <el-menu-item index="/Activities"></el-menu-item>
              <el-menu-item v-if="userid != 666" index="/Calendar"
                ></el-menu-item
              >
              <!-- <el-menu-item index="1-3"></el-menu-item> -->
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title"
              ><i class="el-icon-position"></i></template
            >
            <el-menu-item-group>
              <el-menu-item index="/Map"></el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="3">
            <template slot="title"
              ><i class="el-icon-setting"></i></template
            >
            <el-menu-item-group>
              <el-menu-item index="/Clock"></el-menu-item>
            </el-menu-item-group>
          </el-submenu>

          <!--  -->
          <!-- <el-submenu index="4">
            <template slot="title"
              ><i class="el-icon-plus"></i></template
            >
            <el-menu-item-group>
              
             <el-menu-item index="4"></el-menu-item>
            </el-menu-item-group>
          </el-submenu> -->
        </el-menu>
      </el-aside>

      <!--  -->
      <el-container>
        <el-header style="text-align: right; font-size: 14px">
          <div>
            <!-- divisdebugv-if -->
            <div style="float: left" v-if="!isdebug">
              <span>{{ timestr }}</span>
            </div>
            <div style="float: left" v-else>
              <el-button
                type="primary"
                icon="el-icon-video-pause"
                size="mini"
                @click="stop"
                ></el-button
              ><el-button
                type="primary"
                icon="el-icon-check"
                size="mini"
                @click="backtime"
                ></el-button
              >
              <span>{{ timestr }}</span>
              <el-button
                type="primary"
                icon="el-icon-caret-right"
                size="mini"
                @click="speedup"
                ></el-button
              >
            </div>

            <el-dropdown trigger="click" @command="handle">
              <i class="el-icon-setting" style="margin-right: 14px"></i>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="a">{{ str }}</el-dropdown-item>
                <el-dropdown-item command="b"></el-dropdown-item>
                <el-dropdown-item command="c"></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <span> {{ getname() }} </span>

            <el-dialog title="" :visible.sync="dialogFormVisible">
              <el-form>
                <el-form-item label="" :label-width="formLabelWidth">
                  <el-input placeholder="" v-model="userid"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                  <el-input
                    placeholder=""
                    v-model="password"
                    type="password"
                  ></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                  <el-input
                    placeholder=""
                    v-model="newpassword"
                    type="password"
                  ></el-input>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button
                  @click="
                    dialogFormVisible = false;
                    this.password = '';
                    this.newpassword = '';
                  "
                  > </el-button
                >
                <el-button type="primary" @click="submit"> </el-button>
              </div>
            </el-dialog>
          </div>
        </el-header>

        <div>
          <keep-alive include="Clock">
            <router-view></router-view>
          </keep-alive>
        </div>
        <!-- enter -->
      </el-container>
    </el-container>
  </div>
</template>

<style>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
</style>

<script>
import axios from "axios";
export default {
  data() {
    return {
      astr: "",
      bstr: "",
      realdate: new Date(), //
      speeding: false, //true
      stopping: false,
      isdebug: false, //
      staticdate: new Date(), //speedupmounted
      sdate: new Date(),
      modedate: new Date(),
      datestore: new Date(),
      tick: "",
      interv: 200,
      deinterv: 200,
      userid: "",
      void: "",

      password: "",
      newpassword: "",
      dialogTableVisible: false,
      dialogFormVisible: false,
      formLabelWidth: "120px",
    };
  },
  computed: {
    //,showtime
    timestr: {
      get() {
        return this.showtime.toLocaleString().slice(0, -3) + this.void;
      },
    },
    showtime() {
      if (this.isdebug == true) return this.modedate;
      if (this.stopping == true) return this.datestore;
      else return this.realdate;
    },
    // isdebug,astrbstr
    str() {
      if (this.isdebug == true) {
        return this.bstr;
      } else return this.astr;
    },
  },
  methods: {
    getname() {
      return JSON.parse(sessionStorage.getItem("username"));
    },
    handle(command) {
      console.log("click");
      switch (command) {
        case "a":
          this.isdebug = !this.isdebug;
          this.modedate = this.realdate;
          this.tick = setInterval(() => {
            this.modedate.setTime(this.modedate.getTime() + this.deinterv);
          }, this.interv);
          break;
        case "b":
          console.log("b");
          this.$confirm("", "", {
            iconClass: "el-icon-question",
            confirmButtonText: "",
            cancelButtonText: "",
            showClose: true,
            type: "warning",
          })
            .then(() => {
              this.dialogFormVisible = true;
            })
            .catch(() => {
              //
              this.$message({
                type: "info",
                message: "",
              });
            });

          break;
        case "c":
          sessionStorage.clear();
          this.userid = "";
          this.$router.replace("Login");
          break;
      }
    },
    speedup() {
      // 
      if (this.speeding === true) return;
      this.deinterv = 10 * this.deinterv;
      this.staticdate = this.modedate;
      clearInterval(this.tick);
      this.tick = setInterval(() => {
        this.modedate.setTime(this.modedate.getTime() + this.deinterv);
      }, this.interv);
    },
    stop() {
      this.deinterv = this.interv;
      this.stopping = true;
      this.speeding = false;
      this.datestore = this.showtime;
      clearInterval(this.tick);
    },
    backtime() {
      this.deinterv = this.interv;
      clearInterval(this.tick);
      this.modedate = this.realdate;
      this.tick = setInterval(() => {
        this.modedate.setTime(this.modedate.getTime() + this.deinterv);
      }, this.interv);
      this.speeding = false;
    },
    submit() {
      console.log("edit");
      if (this.userid == "" || this.password == "" || this.newpassword == "") {
        this.$message({ type: "primary", message: "()" });
        return;
      }
      let that = this;
      axios
        .post("http://localhost:3000/users/edit", {
          userid: that.userid,
          password: that.password,
          newpassword: that.newpassword,
        })
        .then((res) => {
          if (res.data.status === 1) {
            // sessionStorage.setItem(
            //   "user",
            //   JSON.stringify(parseInt(res.data.id))
            // );
            // sessionStorage.setItem("login", JSON.stringify(true));
            // sessionStorage.setItem("username", JSON.stringify(res.data.uname));
            // this.$bus.$emit("username")
            this.$message({ type: "success", message: "" });
            // this.$router.replace({ name: 'DisplayCourses' });
          } else this.$message({ type: "error", message: "" });
        })
        .catch((err) => {
          console.log(err);
          this.$message({ type: "primary", message: "" });
        });
      this.password = "";
      this.newpassword = "";
      this.dialogFormVisible = false;
    },
  },
  mounted() {
    this.userid = JSON.parse(sessionStorage.getItem("user"));
    this.$bus.$on("username", this.getname);
    // sessionStorage.setItem("user", JSON.stringify(2020211523));b
    console.log("mounted");
    // 100ms
    // 
    setInterval(() => {
      this.void = " ";
      this.void = "";
      this.realdate = new Date();
      this.$bus.$emit("time", this.showtime);
      this.$bus.$emit("maptime", this.showtime);

      // this.sdate.setTime(
      //   this.staticdate.getTime() +
      //     1800 * (this.realdate.getTime() - this.staticdate.getTime())
      // );
    }, this.interv);
  },
};
</script>